

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}


.btn {
  background-color: #fff;
  color: #8b5cf6;
  padding: 0.8rem 2rem;
  font-weight: bold;
  border-radius: 5px;
  transition: background 0.3s;
}

.btn:hover {
  background-color: #e5e7eb;
}


section {
  padding: 80px 20px;
}




.about {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 50px;
  flex-wrap: wrap;
}

.about img {
  width: 250px;
  border-radius: 50%;
}

.about-text {
  max-width: 500px;
}


.skills {
  text-align: center;
}

.skill-item {
  margin-bottom: 15px;
}

.skill-bar {
  width: 100%;
  background-color: #e0e0e0;
  border-radius: 5px;
  overflow: hidden;
  height: 20px;
}

.skill-progress {
  height: 20px;
  background-color: #8b5cf6;
  width: 0;
  transition: width 1s;
}


.projects {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: center;
}

.project-card {
  width: 300px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 5px 15px rgba(0,0,0,0.2);
  transition: transform 0.3s;
}

.project-card:hover {
  transform: scale(1.05);
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-card .project-info {
  padding: 15px;
}


.contact {
  text-align: center;
}

.contact a {
  display: inline-block;
  margin: 10px;
  color: #8b5cf6;
  font-weight: bold;
}

footer {
  text-align: center;
  padding: 20px;
  background-color: #f3f3f3;
  color: #555;
}


@media (max-width: 768px) {
  .about {
    flex-direction: column;
  }
}